
.carBox{
    margin-top: 46.5px;
    
}
#tbody{
    margin-bottom: 100px;
    
}

#carDiv .tr {
    height: 100px;
    background-color: #ffffff;
    clear: both;
    border-radius: 5px;
    margin-top: 3px;
    
    li {
        display:table-cell;vertical-align:middle;
        float: left;
        height: 100%;
        text-align: center;
        font-size: 14px;
        line-height: 100px;
        img{
            width: 100%;
            height: 100%;
        }
    }
}
#carDiv .carFoot{
    height: 40px;
    background-color: white;
    width: 100%;
    position: fixed;
    border-bottom: 1px solid rgb(223, 222, 222);
    bottom:0.9rem;
    left:0;
    z-index: 9;
    p{
        float: left;
    }
}
  

.msg{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}
